<template>
  <div>
    <div class="cms-list-layout">
      <cms-prompt>
        <li>显示商家运费模板列表</li>
        <li>设置不同运费规则模板</li>
      </cms-prompt>
      <section class="cms-list-query">
        <div class="query-group">
          <label>名称</label>
          <el-input v-model="queryParams.expressName" @change="query"></el-input>
        </div>
        <div class="query-group">
          <label>计价方式</label>
          <el-select v-model="queryParams.tplType" @change="query">
            <el-option label="件数" :value="1"></el-option>
            <el-option label="体积" :value="2"></el-option>
            <el-option label="重量" :value="3"></el-option>
          </el-select>
        </div>
        <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
        </div>
      </section>
        <section class="cms-batch">
          <cms-button icon="success" type="success" size="small" @click="$routerLink('/freight/save','save')">新增</cms-button>
          <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
        </section>
      <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
        <el-table-column type="selection" width="46" fixed='left'></el-table-column>
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="expressName" label="模板名称"></el-table-column>
        <el-table-column prop="useScene" label="是否包邮">
          <template slot-scope="scope">
            <div class="text-overflow">
              <span v-if="scope.row.isFree==0">否</span>
              <span v-else>是</span>
            </div>
          </template>
        </el-table-column>
          <el-table-column prop="tplType" label="计价方式">
          <template slot-scope="scope">
            <div class="text-overflow">
              <span v-if="scope.row.tplType==1">件数</span>
              <span v-if="scope.row.tplType==2">体积</span>
              <span v-if="scope.row.tplType==3">重量</span>
            </div>
          </template>
        </el-table-column>
          <el-table-column prop="useScene" label="创建时间">
          <template slot-scope="scope">
            <span>{{$moment(scope.row.createTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width='175' fixed="right" align="center">
          <div slot-scope="scope">
            <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/freight/update','update',{id:scope.row.id})">编辑</cms-button>
            <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
    </div>
  </div>
</template>

<script>
import list from '@/mixins/list'
import { freightPage, freightDelete } from './../../../api/shop/freight.js'
var defaultData = {
  size: 15,
  page: 1,
  isFree: '',
  tplType: '',
  expressName: ''
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      typeList: []
    }
  },
  computed: {},
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(freightDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(freightDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  created() {
    this.getTableData(freightPage, this.queryParams)
  }
}
</script>

<style>
</style>



